<template>
  <div class="track-wrap">
    <app-header
      title="我的车辆列表"
      showBack="true"
    ></app-header>
    <div class="track-main">
      <div class="ui-title">
        <a @click="addCar" href="javascript:void(0)" style="color: #333;">添加车辆</a>
        <router-link to="/maintain/fuel" style="color:#333; margin-left:2rem;">油耗对比</router-link>
      </div>
      <track-car
        v-for="(item, index) in vehicle"
        :key="index"
        :vehicle="item"
      ></track-car>
    </div>
    <mt-popup
      style="height: 100%; width: 100%; z-index: 1;"
      v-model="addVehicleViewState">
      <vehicle-add></vehicle-add>
    </mt-popup>
  </div>
</template>

<script>
import AppHeader from '../../../components/header/header.vue'
import TrackCar from './track-car.vue'
import VehicleAdd from '../../home/vehicle/vehicle-add.vue'
import {getVehicleList} from '../../../api/home'
import {setLocalStorage} from '../../../lib/util'
import bus from '../../../assets/js/bus'
export default {
  name: 'Track',
  data () {
    return {
      vehicle: [],
      addVehicleViewState: false
    }
  },
  created () {
    this.getDatas()
  },
  mounted () {
    this.$on('goToNextPage', (vin, vle) => {
      this.$router.replace('/track/panel/' + vin)
      setLocalStorage('vle', JSON.stringify(vle))
    })
    bus.$on('clickCloseModal', () => {
      this.addVehicleViewState = false
    })
    bus.$on('addVehicleSuccess', () => {
      this.addVehicleViewState = false
      this.getDatas()
    })
  },
  methods: {
    getDatas () {
      getVehicleList()
        .then(data => {
          if (data.state) {
            this.$set(this, 'vehicle', data.data.list)
          } else {
            this.$toast(data.errormsg || '网络错误')
          }
        })
    },
    addCar () {
      this.addVehicleViewState = true
    }
  },
  components: {
    AppHeader,
    TrackCar,
    VehicleAdd
  }
}
</script>

<style lang="less" scoped>
  .track-wrap{
    height: 100%;
    background-color: #fff;
    .track-main{
      width: 100%;
      position: absolute;
      padding-top: 50px;
      overflow: hidden;
      background-color: #fff;
      &:after{
        display: block;
        width: 100%;
        height: 5rem;
        font-size: .8rem;
        /*content: '我也是有底线的...';*/
        /*border-bottom: 1px solid #ddd;*/
        color: #999;
        text-align: center;
      }
    }
  }
</style>



