<template>
  <div class="car">
    <span>车牌号:{{vehicle.vehicle_license_province}} {{vehicle.vehicle_license_number}}</span>
    <a href="javascript:void(0)" @click="goToNext(vehicle.vin)">查看详情</a>
  </div>
</template>

<script>
export default {
  name: 'Track-Car',
  props: {
    vehicle: Object
  },
  methods: {
    goToNext (vin) {
      this.$parent.$emit('goToNextPage', vin, this.vehicle)
    }
  }
}
</script>

<style lang="less" scoped>
  .car{
    display: flex;
    align-items: center;
    padding: .6rem;
    border-bottom: 1px solid #ddd;
    span{
      display: block;
      flex: 1;
    }
    a{
      display: inline-block;
      padding: 0 1.32em;
      line-height: 2.3;
      font-size: 13px;
      background-color: #1AAD19;
      color: #FFFFFF;
    }
  }
</style>



